@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@100&display=swap');
.Footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #4b3832;
  color: #eee;
  font-size: 5rem;
  height: 200px;
}
.FooterMobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #eee;
  background: #4b3832;
}
.FooterMobile section {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  width: 350px;
  height: 100px;
}

.Footer section {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  width: 350px;
  height: 200px;
}
.Footer section h2 {
  font-family: 'Montserrat Alternates', sans-serif;
  font-size: 1rem;
}
.FooterMobile section h2 {
  font-family: 'Montserrat Alternates', sans-serif;
  font-size: 1rem;
}

.Footer section p {
  font-family: 'Montserrat Alternates', sans-serif;
  font-size: 1rem;
}
.FooterMobile section p {
  font-family: 'Montserrat Alternates', sans-serif;
  font-size: 0.9rem;
}
