.Header {
  height: 100vh;
  background-image: linear-gradient(
      to right,
      rgba(139, 87, 66, 0.7),
      rgba(139, 80, 110, 0.2)
    ),
    url('../Assets/chocolate-cake-g9364fa973_1920.jpg');
  background-size: cover;
  background-position: center;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 90vh, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 80vh, 0 100%);
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: 2rem;
}
.Header h1 {
  font-size: 4rem;
}
.Header section {
  display: flex;
  width: 100%;
}
.Header section svg,
.Header section svg path {
  fill: #eee;

  transition: 1s;
}
.Header section svg:hover,
.Header section svg circle:hover,
.Header section svg path:hover {
  width: 30px;
}

.Rede {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
}
.RedeSvg {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  width: 100vw;
  padding: 1rem;
  height: 100vh;
}
.RedeSvg svg {
  margin: 0.2rem;
}
.DoceMania {
  display: flex;
  justify-content: right;
  position: relative;
  align-items: center;
  height: 100vh;
  width: 100vw;
}
.DoceMania h1 {
  font-size: 1rem;
  color: #eee;
  font-family: 'Dancing Script', cursive;
  transition: 1s;
  margin-left: 1rem;
}
