.contatoContainer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: fixed;
  right: 0;
  top: 34%;
  width: 10rem;
  height: 3rem;
  margin: 0.6rem;
  background: rgb(105, 38, 5);
  background: linear-gradient(
    220deg,
    rgba(105, 38, 5, 1) 10%,
    rgba(180, 179, 189, 1) 100%
  );
  border-radius: 0.9rem 0.9rem 0;
}
.contatoContainer svg {
  fill: #4b3832;
  width: 30px;
}
.contatoContainer p {
  font-size: 1rem;
}
