.Menu {
  display: flex;
  justify-content: right;
  width: 100%;
  height: 6rem;
  padding: 1rem;
  list-style: none;
  transition: 2s;
  filter: drop-shadow(0 0 6rem rgba(139, 87, 66, 0.7));
}

.Menu li {
  color: #eee;
  margin-left: 1rem;
  border-radius: 0.7rem;
  padding: 0.3rem;
  transition: 1s;

  font-family: 'Dancing Script', cursive;
}
.Menu li:hover {
  color: #eee;
  filter: drop-shadow(0.1rem 0 0.6rem #4b3832);
}
