@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Parisienne&display=swap');

.Produtos {
  display: flex;
  position: relative;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.BarnerProdutos {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Produtos h1 {
  font-size: 3rem;
  font-family: 'Dancing Script', cursive;
}
.Produtos img {
  margin-top: 1rem;

  border-radius: 0.4rem;
  width: calc(100vw - 26rem);
}

.Card {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  margin-bottom: 1rem;
}

.Card section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 450px;
}

.Texto {
  display: flex;
  margin-top: 4rem;
  align-items: center;
  flex-direction: row;
  border-radius: 0.4rem;
  width: 300px;
  max-height: 200px;
}
.Card h6 {
  color: #4b3832;
  font-family: 'Dancing Script', cursive;
  font-size: 2rem;
}
.Card p {
  font-family: 'Parisienne', cursive;
  font-size: 1.8rem;
}

.ProdutosImg img {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
}
.ProdutosMobile {
  display: flex;
  justify-content: sp;
  flex-direction: column;
}
.ProdutosMobile img {
  border-radius: 0.5rem;
  width: calc(100vw - 3rem);
}
.ProdutosImgMobile img {
  width: 60%;
}
.ProdutosMobile h1 {
  font-size: 2rem;
}
.ProdutosMobile h6 {
  margin-top: 3rem;
}
.TextoMobile {
  width: 1000px;
}
